
import { Component, Vue } from "vue-property-decorator";
import HelloWorld from "@/components/HelloWorld.vue";
// import About from './About.vue';

@Component({
  components: {
    HelloWorld
  }
})

export default class Home extends Vue {
  msg: string = "こんちは！！";
  recognizing: boolean = false;
  recognition: any = null;
  recordBtnValue = "録音";
  recoredTexts: string = "";
  recoredMiniText: string = "";
  size_text:string="";

  async created() {
    const { webkitSpeechRecognition } = window as any;
    const recognition = new webkitSpeechRecognition();
    recognition.lang = "ja-JP";
    recognition.continuous = true;
    recognition.onresult = await this.recognize;
    this.recognition = recognition;
  }

  record() {
    if (this.recognizing) {
      this.recognition.stop();
      this.recordBtnValue = "録音";
    } else {
      this.recognition.start();
      this.recordBtnValue = "停止";
    }
    this.recognizing = !this.recognizing;
  }

  // e:SpeechRecognitionEvent
  protected async recognize(e: any) {
    this.recoredMiniText += `${e.results[e.results.length - 1][0].transcript}\n`;
    if(/\d/.test(this.recoredMiniText)){
      this.recoredTexts+=`${e.results[e.results.length - 1][0].transcript}\n`;
      this.recoredMiniText="";
    }else if(this.recoredMiniText.includes("トップ")){
      this.recognition.stop();
      this.recordBtnValue ="録音";
      this.recoredMiniText="";
    }else{
      console.log(this.recoredMiniText);
    }
  }
/*   SaveText(){
     const savetext = this.recoredTexts;
     const blob = new Blob([savetext],{type:'text/plain'});
     const link = document.createElement('a');
     link.href = URL.createObjectURL(blob);
     link.download = 'file.txt';
     link.click();
  } */
  ResetTexts(){
    this.recoredMiniText = "";
    this.recoredTexts = "";
  }
/* ページのリロードに関する処理 */
  beforeUnload(event: BeforeUnloadEvent) {
    event.preventDefault();
    // this.SaveText();
    console.log("Hey");
    event.returnValue = "ページを離れる前に記録内容をテキストファイルに保存しませんか？";
  }
  mounted() {
    window.addEventListener("beforeunload", this.beforeUnload);
  }
  beforeDestroy() {
    window.removeEventListener("beforeunload", this.beforeUnload);
  }
}

